import React from 'react';


export default function Header(){
    return(
    <React.Fragment>
        <header class="masthead">
            <div class="container h-100">
                <div class="row h-100 align-items-center justify-content-center text-center">
                    <div class="col-lg-10 align-self-end">
                        <h1 class="text-uppercase text-white font-weight-bold">Learning Live!  Learning Strong!</h1>
                        <hr class="divider my-4" />
                    </div>
                    <div class="col-lg-8 align-self-baseline">
                        <p class="text-white-75 font-weight-light mb-5">Test your knowledge through mock tests.</p>
                        <a class="btn btn-primary btn-xl js-scroll-trigger" href="tests">Take Test Now</a>
                    </div>
                </div>
            </div>
        </header>
    </React.Fragment>
    )
}